import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TreePickerComponent } from './container/tree-picker.component';

const routes: Routes = [
  { path: '', component: TreePickerComponent }
];

@NgModule({
  imports: [ RouterModule.forChild(routes) ],
  exports: [ RouterModule ]
})
export class TreePickerRoutingModule { }
